import React from 'react';
import clsx from 'clsx';
import { createStyles, Theme, withStyles, WithStyles } from '@material-ui/core/styles';
import { Omit } from '@material-ui/types';
import Drawer, { DrawerProps } from '@material-ui/core/Drawer';
import Divider from '@material-ui/core/Divider'
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import AssignmentIcon from '@material-ui/icons/Assignment'; // projects
import SchoolIcon from '@material-ui/icons/School'; // education
import WorkIcon from '@material-ui/icons/Work'; // work experience

import Content from './Content';

export interface INavigatorLink {
  id: string;
  icon: JSX.Element;
  content: JSX.Element;
  active?: boolean;
}

export interface NavigatorProps extends Omit<DrawerProps, 'classes'>, WithStyles<typeof styles> {
  onLinkClick: (content: INavigatorLink) => void;
}

const categories = [
  {
    id: 'History',
    children: [
      { id: 'Experience', icon: <WorkIcon />, content: <Content dataType="work" />, active: true},
      { id: 'Education', icon: <SchoolIcon />, content: <Content dataType="education" />},
      { id: 'Projects', icon: <AssignmentIcon />, content: <Content dataType="projects" />},
    ],
  },
];

const styles = (theme: Theme) =>
  createStyles({
    categoryHeader: {
      paddingTop: theme.spacing(2),
      paddingBottom: theme.spacing(0.25),
    },
    categoryHeaderPrimary: {/* color: theme.palette.primary.contrastText, */},
    item: {
      paddingTop: 1,
      paddingBottom: 1,
    },
    itemCategory: {
      //boxShadow: '0 -1px 0 #404854 inset',
      paddingTop: theme.spacing(2),
      paddingBottom: theme.spacing(2),
    },
    firebase: {
      //fontSize: 24,
      //color: theme.palette.common.white,
    },
    itemActiveItem: {
      //color: '#4fc3f7',
      //color: theme.palette.secondary.main,
    },
    itemPrimary: {
      fontSize: 'inherit',
    },
    itemIcon: {
      minWidth: 'auto',
      marginRight: theme.spacing(2),
    },
    divider: {
      marginTop: theme.spacing(2),
    },
  });

function Navigator(props: NavigatorProps) {
  const { classes, ...other } = props;
  const handleListItemClick = (item: INavigatorLink) => {
    // TODO
  };

  return (
    <Drawer variant="permanent" {...other}>
      <List disablePadding>
        <ListItem className={clsx(classes.firebase, classes.item, classes.itemCategory)}>
          <Typography variant="h6">
            Richard Poulson
          </Typography>
        </ListItem>
        <Divider variant='middle'/>
        {/* For each link category in the categories object, map to a ListItemText. */
        categories.map(({ id, children }) => (
          <React.Fragment key={id}>
            <ListItem className={classes.categoryHeader}>
              <ListItemText
                classes={{
                  primary: classes.categoryHeaderPrimary,
                }}
              >
                {id}
              </ListItemText>
            </ListItem>
              {/* For link in a link category, map to a ListItemIcon and a ListItemText, setting onClick to onLinkClick prop. */
              children.map(({ id: childId, icon: childIcon, content: childContent, active }) => (
                <ListItem
                  key={childId}
                  button
                  className={clsx(classes.item, active && classes.itemActiveItem)}
                  onClick={() => props.onLinkClick({id: childId, icon: childIcon, content: childContent})}
                >
                  <ListItemIcon className={classes.itemIcon}>{childIcon}</ListItemIcon>
                  <ListItemText
                    classes={{
                      primary: classes.itemPrimary,
                    }}
                  >
                    {childId}
                  </ListItemText>
                </ListItem>
              ))}
          </React.Fragment>
        ))}
      </List>
    </Drawer>
  );
}

export default withStyles(styles)(Navigator);
